import './main.css';

const appId = document.getElementById('app-id') as HTMLInputElement;
const isProduction = document.getElementById('is-production') as HTMLInputElement;

const mobileLink = document.getElementById('link-mobile-link') as HTMLLinkElement;
const webLink = document.getElementById('link-web-link') as HTMLLinkElement;
const myAccount = document.getElementById('my-account-link') as HTMLLinkElement;

const URLS = {
  STAGING: {
    LINK_MOBILE:
      'https://wwws-staging.moneytree.jp/link/mobile/#/app/oauth/authorize?redirect_uri=urn:ietf:wg:oauth:2.0:oob&response_type=token&scope=guest_read%20accounts_read&client_id=',
    LINK_WEB:
      'https://wwws-staging.moneytree.jp/link/#/app/oauth/authorize?redirect_uri=urn:ietf:wg:oauth:2.0:oob&response_type=token&scope=guest_read%20accounts_read%20transactions_read%20request_refresh&configs=back_to%3Dgoogle.com&lang=ja&client_id=',
    MY_ACCOUNT:
      'https://myaccount-staging.getmoneytree.com/oauth/authorize?redirect_uri=urn%3Aietf%3Awg%3Aoauth%3A2.0%3Aoob&response_type=token&scope=guest_read%20accounts_read&locale=en&client_id='
  },
  PRODUCTION: {
    LINK_MOBILE:
      'https://www.moneytree.jp/link/mobile/#/app/oauth/authorize?redirect_uri=urn:ietf:wg:oauth:2.0:oob&response_type=token&scope=guest_read%20accounts_read&client_id=',
    LINK_WEB:
      'https://www.moneytree.jp/link/#/app/oauth/authorize?redirect_uri=urn:ietf:wg:oauth:2.0:oob&response_type=token&scope=guest_read%20accounts_read%20transactions_read%20request_refresh&configs=back_to%3Dgoogle.com&lang=ja&client_id=',
    MY_ACCOUNT:
      'https://myaccount.getmoneytree.com/oauth/authorize?redirect_uri=urn%3Aietf%3Awg%3Aoauth%3A2.0%3Aoob&response_type=token&scope=guest_read%20accounts_read&locale=en&client_id='
  }
};

function update() {
  const links = isProduction.checked ? URLS.PRODUCTION : URLS.STAGING;

  const appIdValue = appId.value;

  mobileLink.href = `${links.LINK_MOBILE}${appIdValue}`;
  webLink.href = `${links.LINK_WEB}${appIdValue}`;
  myAccount.href = `${links.MY_ACCOUNT}${appIdValue}`;
}

function init() {
  appId.addEventListener('change', update, false);
  isProduction.addEventListener('change', update, false);
}

init();
